<template>
    <div>
        <input type="checkbox" id="nav-toggle" hidden>
	    <nav class="nav">
            <label for="nav-toggle" class="nav-toggle" onclick></label>
            <div class="user-info">
                <span class="data">{{date | date('datetime')}}</span>
                <img src="../assets/duck.png" alt="УУУТКАААА" width="40%" align="center" hspace="30%" vspace="10%" >
                <div class="input_par">
                    User:
                </div>
                <div class="input_par">
                    <button class="btn_logout" @click.prevent="logout">Logout</button>
                </div>
            </div>
        </nav>

         <div class="info_dev">
			<div class="info_dev_text">
				<p>The work was performed by Yuri Batalin and Egor Mitrofanov</p>
				Variant 81327
			</div>
		</div>
        <div class="cnv">
          <canvas id="canvas" width="500" height="500"></canvas>
        </div>
        <form method="POST">
            <div class="input_param_side">
                <div class="input_side">
                    <div class="input_par">
                        Input cord X:
                    </div>
                    <div class="input_par">
                        <input type="radio" id="x_1" name="X" value="-4" >-4
                        <input type="radio" id="x_2" name="X" value="-3" >-3
                        <input type="radio" id="x_3" name="X" value="-2" >-2
                        <input type="radio" id="x_4" name="X" value="-1" >-1
                        <input type="radio" id="x0" name="X" value="0" checked>0
                        <input type="radio" id="x" name="X" value="1" >1
                        <input type="radio" id="x2" name="X" value="2" >2
                        <input type="radio" id="x3" name="X" value="3" >3
                        <input type="radio" id="x4" name="X" value="4" >4
                    </div>
                    <div class="input_par">
                        Input cord Y:
                    </div>
                    <div class="input_par">
                        <input class="input" type="text" name="y" id="y" placeholder="Input cord Y">
                    </div>
                    <div class="input_par">
                        Input R:
                    </div>
                    <div class="input_par">
                        <input type="radio" id="r1" value="1" v-model="inputR" v-on:click="draw(1)">
                        <label for="r1">1</label>
                        <input type="radio" id="r2" value="2" v-model="inputR" v-on:click="draw(2)">
                        <label for="r1">2</label>
                        <input type="radio" id="r3" value="3" v-model="inputR" v-on:click="draw(3)">
                        <label for="r1">3</label>
                        <input type="radio" id="r4" value="4" v-model="inputR" v-on:click="draw(4)" checked>
                        <label for="r1">4</label>
                    </div>
                    <div class="input_par">
                        <button class="btn_in_param">Input</button>
                    </div>
                </div>
            </div>
        </form>
        <div class="table-area">
			<div class="input_par">
				<table class="result-table">
					<thead>
						<td>X</td>
						<td>Y</td>
						<td>R</td>
						<td>RESULT</td>
					</thead>
					<tbody>
					</tbody>
				</table>
			</div>
		</div>
    </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'

export default {
    data: () => ({
        date: new Date(),
        interval: null,
        inputR: 4,
        x: '',
        y: '',
        r: ''
    }),
    methods: {
        logout() {
            Vue.prototype.token = ''
            this.$router.push('/login?message=logout')
        },
        submitDataPoints(){
            axios.post('http://localhost:8080/rest/main', ).then(function (response) {
                console.log(response)
            }).catch(function (error) {
                console.log(error);
            });
        },
        draw(value){
          let canvas = document.getElementById("canvas");
          let ctx = canvas.getContext("2d");
          ctx.font = 'bold 15px sans-serif';
          let R = value
          ctx.clearRect(0, 0, 1000, 1000);

          ctx.fillStyle = "rgba(91,95,201,1)";
          ctx.beginPath();
          ctx.moveTo(250, 250);
          //отображаем сектор круга
          ctx.arc(250, 250, 200, Math.PI, Math.PI/2, true);
          //отображаем прмоугольник
          ctx.moveTo(250, 250);
          ctx.lineTo(50,250);
          ctx.lineTo(50,150);
          ctx.lineTo(250,150);
          ctx.lineTo(250,250);
          //отображаем треугольник
          ctx.moveTo(250,250);
          ctx.lineTo(250,450);
          ctx.lineTo(350,250);
          ctx.fill();

          ctx.fillStyle = "rgba(0, 0, 0, 1)";
          ctx.beginPath();
          //отображение линии x
          ctx.moveTo(0,250);
          ctx.lineTo(500,250);

          //отрисовка стрелк x
          ctx.lineTo(485,255);
          ctx.moveTo(500,250);
          ctx.lineTo(485,245);

          //отображение линии y
          ctx.moveTo(250,0);
          ctx.lineTo(250,500);
          //отрисовка стрелк y
          ctx.moveTo(250,0);
          ctx.lineTo(245,15);
          ctx.moveTo(250,0);
          ctx.lineTo(255,15);

          //Всё для оси X
          ctx.fillText(R/2,350,270);
          ctx.moveTo(350,255);
          ctx.lineTo(350,245);
          ctx.fillText(R,440,270);
          ctx.moveTo(440,255);
          ctx.lineTo(440,245);
          ctx.fillText(-R/2,150,270);
          ctx.moveTo(150,255);
          ctx.lineTo(150,245);
          ctx.fillText(-R,50,270);
          ctx.moveTo(50,255);
          ctx.lineTo(50,245);
          ctx.fillText("X",485,240);

          //Всё для оси Y
          ctx.fillText(R/2,255,150);
          ctx.moveTo(245,150);
          ctx.lineTo(255,150);
          ctx.fillText(R,255,50);
          ctx.moveTo(245,50);
          ctx.lineTo(255,50);
          ctx.fillText(-R/2,255,350);
          ctx.moveTo(245,350);
          ctx.lineTo(255,350);
          ctx.fillText(-R,255,450);
          ctx.moveTo(245,450);
          ctx.lineTo(255,450);
          ctx.fillText("Y",260,20);

          ctx.closePath();
          ctx.stroke();
        }
    },
    mounted() {
      this.draw(this.inputR)
      this.interval = setInterval(() => {
        this.date = new Date()
      }, 1000)
      console.log(this.token)
    },
    beforeDestroy(){
        clearInterval(this.interval)
    }
}
</script>